import React, { PropsWithChildren, useState } from "react";
import { cl } from "../helpers/general";
import { GRADIENT_1 } from "../styles";

interface Props extends PropsWithChildren {
  title: string;
}

export const AccordionItem = (props: Props) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className="w-full my-8">
      <div
        onClick={() => setIsOpen(!isOpen)}
        className="text-xl sm:text-3xl pb-8 cursor-pointer"
      >
        <span
          className={cl(
            isOpen && `text-transparent bg-clip-text ${GRADIENT_1}`
          )}
        >
          {props.title}
        </span>
      </div>
      {isOpen && <div className="leading-7 pb-8">{props.children}</div>}
      <div
        className={cl(
          "w-full bg-gradient-to-r transition-all to-transparent",
          isOpen
            ? "h-1 rounded-sm mb-16 from-brand-ten"
            : "from-white opacity-25 h-0.5"
        )}
      />
    </div>
  );
};
