import * as React from "react";
import Helmet from "react-helmet";
import { useStaticQuery, graphql } from "gatsby";
import { PortableText } from "@portabletext/react";
//
import { Footer } from "../components/Footer";
import { Header } from "../components/Header";
import { PageIntro } from "../components/PageIntro";
import { BottomBanner } from "../components/BottomBanner";
import { LayoutContainer } from "../components/LayoutContainer";
import { AccordionItem } from "../components/AccordionItem";
//

const Explore = () => {
  const rawData = useStaticQuery(graphql`
    query {
      allSanityFaq {
        nodes {
          question
          _rawAnswer
        }
      }
    }
  `);

  const data: any[] = rawData.allSanityFaq.nodes;

  return (
    <>
      <Header hasPageIntro={true} />
      <PageIntro title="FAQ" subTitle="Find your answers here." />
      <LayoutContainer className="sm:py-24">
        {data.map((item, index) => {
          return (
            <AccordionItem key={index} title={item.question}>
              <PortableText value={item._rawAnswer} components={{
                marks: {
                  link: ({ children, value }) => {
                    return (
                      <a href={value.href} className="text-brand-nine" target="_blank">
                        {children}
                      </a>
                    )
                  },
                }
              }} />
            </AccordionItem>
          );
        })}
      </LayoutContainer>
      <BottomBanner />
      <Footer />
    </>
  );
};

export default Explore;
